






















import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})

export default class Nav extends Vue {
  @Prop({ type: String, default: ''}) public readonly title!: string;
  @Prop({ type: Boolean, default: false }) public readonly isLeftArrow!: false;
  @Prop({ type: Boolean, default: false }) public readonly isLeftText!: false;
  @Prop({ type: Boolean, default: false }) public readonly isRightText!: false;
  @Prop({ type: Boolean, default: false }) public readonly isHeadSetIcon!: false;
  @Prop({ type: String, default: '#000' }) public readonly headSetColor!: string;
  @Prop({ type: String, default: '#000' }) public readonly leftArrowColor!: string;
  private goBackPage() {
    this.$router.back();
  }
}
