import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  {
    path: '/home',
    name: 'Home',
    alias: '/',
    component: () => import('@/views/home/Home.vue')
  },
  {
    path: '/recharge',
    name: 'Recharge',
    component: () => import('@/views/recharge/Recharge.vue')
  },
  {
    path: '/promotion',
    name: 'Promotion',
    component: () => import('@/views/promotion/Promotion.vue')
  },
  {
    path: '/live',
    name: 'live',
    
    component: () => import('../views/live/index.vue')
  },
  {
    path: '/top',
    name: 'Top',
    component: () => import('@/views/top/Top.vue')
  },
  {
    path: '/usersearch',
    name: 'UserSearch',
    component: () => import('@/views/usersearch/UserSearch.vue')
  },
  {
    path: '/user/onlineShopping',
    name: 'onlineShopping',
    
    component: () => import('../views/user/onlineShopping.vue')
  },
  {
    path: '/user/personalization',
    name: 'personalization',
    
    component: () => import('../views/user/personalization.vue')
  },
  {
    path: '/user/certification',
    name: 'certification',
    
    component: () => import('../views/user/certification.vue')
  },
  {
    path: '/user/authentication',
    name: 'authentication',
    
    component: () => import('../views/user/authentication.vue')
  },
  {
    path: '/user/detail',
    name: 'detail',
    
    component: () => import('../views/user/detail.vue')
  },
  {
    path: '/user/earning',
    name: 'earning',
    
    component: () => import('../views/user/earning.vue')
  },
  {
    path: '/user/grade',
    name: 'grade',
    
    component: () => import('../views/user/grade.vue')
  },
  {
    path: '/user/equipment',
    name: 'equipment',
    
    component: () => import('../views/user/equipment.vue')
  },
  {
    path: '/user/withdraw',
    name: 'withdraw',
    
    component: () => import('../views/user/withdraw.vue')
  },
  {
    path: '/user/bet',
    name: 'bet',
    
    component: () => import('../views/user/bet.vue')
  },
  {
    path: '/user/historique',
    name: 'historique',
    
    component: () => import('../views/user/historique.vue')
  },
  {
    path: '/user',
    name: 'user',
    
    components: {
      default: () => import('@/views/user/index.vue'),
    }
  },

  {
    path: '/gamelist',
    name: 'GameList',
    component: () => import('@/views/gamelist/GameList.vue')
  },
  
]

const router = new VueRouter({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router
