import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    newsClose: false,
  },
  mutations: {
    setNewsClose(state, status) {
      state.newsClose = status;
    }
  },
  actions: {
    setNewsClose({ commit }, payload) {
      commit('setNewsClose', payload);
    }
  },
  modules: {
  }
})
