




















import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})
export default class Footer extends Vue {
  private active?: string = '';
  private showTchest?: boolean = false;
  @Prop({ type: String, default: '首页' }) public readonly tabActive!: string;
  private footerItems: any[] = [
    {
      text: '首页',
      img: 'main_bottomtab_home',
      link: '/',
    },
    {
      text: '充值',
      img: 'main_bottomtab_circle',
      link: '/recharge',
    },
    {
      text: '宝箱',
      img: 'main_bottom_baoxian',
      link: '',
    },
    {
      text: '游戏',
      img: 'main_bottomtab_play',
      link: '/gamelist',
    },
    {
      text: '我的',
      img: 'main_bottomtab_mine',
      link: '/user',
    },
  ];
  private getMenuImage(img: string, itemText: string, activeText: string) {
    if (img) {
      if (activeText === itemText) {
        return require(`@/assets/img/footer/${img}_sel.png`);
      } else {
        return require(`@/assets/img/footer/${img}_normal.png`);
      }
    }
    return '';
  }
  private goPage(url: string, index: number) {
    if (index == 2) {
      this.showPopup();
    } else {
      const currName = this.$route.path;
      if (currName != url) {
        this.$router.push(url);
      }
    }
  }
  private showPopup(): void {
    this.showTchest = true;
  }
  private hidePopup(v: boolean) {
    this.showTchest = v;
  }
  private mounted () {
    this.active = this.tabActive;
  }
}
